import React from "react";
import { Link } from "react-router-dom";
import { motion } from "framer-motion";
import { fade, titleAnim } from "../Animation";

const Hero = () => {
  return (
    <div className="bg-white h-screen flex flex-col justify-center items-center">
      <motion.h1
        variants={titleAnim}
        className="lg:text-7xl md:text-6xl sm:text-5xl text-4xl font-black mb-14"
      >
        We're EGG<span className="text-yellow-500">CELLENT</span>
      </motion.h1>
      <motion.div variants={fade}>
        <Link
          className="py-3 px-5 bg-yellow-500 rounded-full text-2xl hover:bg-black hover:text-yellow-500 transition duration-300 ease-in-out flex items-center"
          to="/menu"
        >
          Order Now{" "}
          <svg
            className="w-6 h-6 ml-4"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z"
            />
          </svg>
        </Link>
      </motion.div>
    </div>
  );
};

export default Hero;
